#content-header {
    display: grid;
    grid-template-columns: repeat(5, 20%);
    margin-bottom: 20px;
    background-color: gray;
    padding: 5px 0;
    border-radius: 5px;
}

.content-header-field {
    margin: 0 5px;
    border-radius: 5px;
    background-color: lightgray;
    padding: 5px;
    font-size: 1.2rem;
    font-weight: bold;
    display:flex;
    align-items: center;
    justify-content: center;
}




input {
    padding: 5px 0;
    text-align: center;
    border-radius: 5px;
    font-size: 1rem;
}

textarea {
    text-align: center;
}

#addButton {
    float: right;
    margin: 10px 0 50px 0;
    width: 20%;
}

.cursor {
    cursor: pointer;
}